.Converter {
    width: 100%;
    padding: 0 15%;
    box-sizing: border-box;
    text-align: center;
  }.Form {
    display: flex;
    margin: 0 auto;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
  }.Form input,
  select {
    padding: 5px;
    margin: 5px 10px;
    border-radius: 5px;
    border: 1px solid rgba(119, 119, 119, 0.5);
  }.Form button {
    padding: 5px 10px;
    border-radius: 5px;
    background: rgb(11, 170, 59);
    color: white;
    border: none;
  }.Converter h3 {
    background-color: #ccc;
    border-radius: 10px;
    padding: 15px;
    font-size: 30px;
  }.Converter h2 span {
    color: rgb(11, 170, 59);
  }
.header {
  width: 100%;
  height: auto;
  display: flex;
  background-color: rgb(78, 72, 63);
  padding: 0 50px;
}

.header-logo {
  display: flex;
  height: 100px;
}

.footer {
  background-color: rgb(78, 72, 63);
  display: flex;
  flex-shrink: 0;
  width: 100%;
  height: auto;
  left: 0;
  bottom: 0;
  align-items: center;
  padding: 0 50px;
  justify-content: space-evenly;
}

.footer-brand {
  text-align: justify;
  color: #fff;
  font-size: 13px;
  width: 60%;
  min-width: 300px;
  height: auto;
  padding: 0 20px;
}

@media screen and (max-width: 700px) {
  .footer-brand-size {
    font-size: 10px;
  }
}

.footer-logo {
  display: flex;
}

@media screen and (max-width: 600px) {
  .footer-position {
    flex-direction: column;
    padding: 10px;
  }
}

.carousel {
  flex: 1 1;
  text-align: center;
  position: relative;
  -webkit-mask-image: -webkit-gradient(linear, left bottom, left top, color-stop(1%, transparent), color-stop(20%, #f0f2f5));
  -webkit-mask-image: linear-gradient(to top, transparent 1%, #f0f2f5 20%);
  mask-image: -webkit-gradient(linear, left bottom, left top, color-stop(1%, transparent), color-stop(20%, #f0f2f5));
  mask-image: linear-gradient(to top, transparent 1%, #f0f2f5 20%);
}

.carousel-text {
  font-size: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-family: "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", Osaka,
    メイリオ, Meiryo, "ＭＳ Ｐゴシック", "MS PGothic", "ＭＳ ゴシック",
    "MS Gothic", "Noto Sans CJK JP", TakaoPGothic, sans-serif;
  color: black;
}

.team-card {
  display: flex;
  font-size: 20px;
  font-weight: bold;
  color: #f0f2f5;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

@media screen and (max-width: 500px) {
  .team-card-space {
    padding: 20px 0;
  }
}

.team-card-photo {
  width: 80%;
  height: auto;
  margin-bottom: 10px;
  -webkit-transition: -webkit-transform 0.2s;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
  -webkit-clip-path: circle(50% at center);
          clip-path: circle(50% at center);
}

.team-card-photo:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.Thomas {
  font-family: Helvetica;
  background: #f0f2f5;
}

.main {
  background: url(/static/media/clouds.da4c13db.png) fixed bottom no-repeat;
}

.contact {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  align-items: center;
  padding: 30px 0;
  background: #f0f2f5;
}

@media screen and (max-width: 800px) {
  .contact-position {
    flex-direction: column;
  }
}

.map-img {
  position: relative;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  width: 40%;
  min-width: 20%;
}

@media screen and (max-width: 800px) {
  .map-size {
    width: 70%;
  }
}

@media screen and (min-width: 1680px) {
  .city-height {
    height: 950px;
  }
}

@media screen and (max-width: 1679px) {
  .city-height {
    height: 800px;
  }
}

@media screen and (max-width: 1379px) {
  .city-height {
    height: 560px;
  }
}

@media screen and (max-width: 979px) {
  .city-height {
    height: 380px;
  }
}

@media screen and (max-width: 679px) {
  .city-height {
    height: 300px;
  }
}

@media screen and (max-width: 500px) {
  .city-height {
    height: 180px;
  }
}

.parallax {
  display: flex;
  width: 100%;
  background: url(/static/media/transition_image.10d20418.png);
  background-repeat: no-repeat;
  background-size: 100%;
  position: relative;
  bottom: 0;
}

.mask {
  display: flex;
  -webkit-mask: url(data:image/png;base64,);
          mask: url(data:image/png;base64,);
  mask-size: cover;
  -webkit-mask: url(data:image/png;base64,);
  -webkit-mask-size: cover;
  width: 100%;
  height: 100%;
}

.clouds {
  z-index: 50;
  position: fixed;
  bottom: 0;
  width: 100%;
}

.team {
  display: flex;
  width: 100%;
  background: #37363d;
  padding: 20px 0;
  justify-content: center;
}

@media screen and (min-width: 501px) {
  .team-position {
    flex-direction: row;
  }
}

@media screen and (max-width: 500px) {
  .team-position {
    flex-direction: column;
  }
}

.down-arrow-image{
  width: 20px;
  height: auto;
}


