/* https://images.unsplash.com/photo-1439902315629-cd882022cea0?fit=crop&fm=jpg&h=975&q=80&w=1925 */
.hero{
	height: 100vh;
	width: 100%;
	position: fixed;
	top: 0;
	z-index: 9;
	-webkit-transition: all 1.6s cubic-bezier(0.86, 0, 0.07, 1);
	transition: all 1.6s cubic-bezier(0.86, 0, 0.07, 1);
}
.hero.scrolled{
	-webkit-transform: translate3d(0, -100%, 0) scale(.75);
	        transform: translate3d(0, -100%, 0) scale(.75);
	opacity: 0;
}
.hero-inner{
	background-image: url('https://files.hipcdn.com/photos/thailand/5599801a70726f1f360003e0/5d89977ca12eda0c54000533/walden-thonglor-8-condo-watthana-5dc0f129a12eda20990137ed_full.jpg');
	background-size: cover;
	background-position: center;
	display: table;
	width: 100%;
	height: 100vh;
	top: 0;
	position: fixed;
}
.hero-title{
	display: table-cell;
	vertical-align: middle;	
	text-align: center;
}
h1, h2, h3, h4, h5, h6{
	font-family: montserrat;
}
.font-2{
	font-family: 'josefin sans';
	font-weight: 700;
}
.title{ letter-spacing: .3em; text-transform: uppercase; }
.text-light{ color: #fff }
.font-alt{
	font-family: 'georgia';
	font-style: italic;
	color: #666;
}

.hero{
	overflow: hidden;
	z-index: 1;
}
.content{
	position: relative;
	background-color: #fff;
	border-top: 10px solid black;
	padding: 0;	
	margin: 0;
	-webkit-transition: all 2.5s cubic-bezier(0.86, 0, 0.07, 1);
	transition: all 2.5s cubic-bezier(0.86, 0, 0.07, 1);
	-webkit-transform: translate3d(0, 10px, 0) scale(.35);
	        transform: translate3d(0, 10px, 0) scale(.35);
	opacity: 0;
}
.content.scrolled{
	-webkit-transform: translate3d(0, 0, 0) scale(1);
	        transform: translate3d(0, 0, 0) scale(1);	
	opacity: 0.98;
	position: relative;
}
.sd{
	color: black;
	position: absolute;
	bottom: 20px;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
}
.sd:hover, .sd:focus{
	color: #fff;
	opacity: .7;
	text-decoration: none;
}
/* work in progrece */
@media only screen and (max-width: 1200px) {
	.hero-inner{
	
			background-size:100% auto;
		
	}
}
